import React from 'react';
import Fade from 'react-reveal/Fade';
import { Link } from 'gatsby';
import './listboard.css';
//import abstractvs from '../../images/abstractvs.jpg';
import adrenaline from '../../images/adrenaline.jpg';
import akfaUniversity from '../../images/akfa-university.jpg';
import bars from '../../images/bars.jpg';
import bon from '../../images/bon.jpg';
import chekhov from '../../images/chekhov.jpg';
//import givenchy from '../../images/givenchy.jpg';
import glamour from '../../images/glamour.jpg';
import hp from '../../images/hp.jpg';
import humoArena from '../../images/humo-arena.jpg';
//import podiumItalia from '../../images/podium-italia.jpg';
//import ravnaqBank from '../../images/ravnaq-bank.jpg';
import terraPro from '../../images/terra-pro.jpg';
import texnomart from '../../images/texnomart.jpg';
//import theBlackQuail from '../../images/the-black-quail.jpg';
//import twba from '../../images/twba.jpg';

const Listboard = () => (
    <div className="listboard column">
        <Fade bottom>
        <div className="card card-column" style={{'gridRowEnd': 'span 21'}}>
            <div className="content">
                <Link to="/project" className="card-video"
                 state={{ title: 'Akfa University', videoUrl: 'https://youtube.com/embed/2y6uzBOldDA' }}>
                <div className="card-video-inner">
                    <img src={akfaUniversity} alt="Akfa University" />
                </div>
                <span className="card-link">Akfa University</span>
                <p>Designing a website for innovative projects by Mozilla</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 19', 'marginTop': '30px'}}>
            <div className="content">
                <Link to="/project" className="card-video"
                 state={{ title: 'Adrenaline Rush Uzbekistan', videoUrl: 'https://www.youtube.com/embed/258IWEORXPI' }}>
                <div className="card-video-inner">
                    <img src={adrenaline} alt="Adrenaline Rush Uzbekistan" />
                </div>
                <span className="card-link">Adrenaline Rush Uzbekistan</span>
                <p>Official Firefox rebrand</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 21'}}>
            <div className="content">
                <Link to="/project" className="card-video"
                 state={{ title: 'Glamour Boutique', videoUrl: 'https://youtube.com/embed/4cA-48a7PCU' }}>
                <div className="card-video-inner">
                    <img src={glamour} alt="Glamour Boutique" />
                </div>
                <span className="card-link">Glamour Boutique</span>
                <p>Apple TV app UI development</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 22'}}>
            <div className="content">
                <Link to="/project" className="card-video"
                 state={{ title: 'Bars Tires', videoUrl: 'https://youtube.com/embed/y_3YpDLeV7M' }}>
                <div className="card-video-inner">
                    <img src={bars} alt="Bars Tires" />
                </div>
                <span className="card-link">Bars Tires</span>
                <p>Developing micro-interactions for the Lightning Design System</p>
                </Link>
            </div>
        </div>
        
        <div className="card card-column" style={{'gridRowEnd': 'span 22'}}>
            <div className="content">
                <Link to="/project" className="card-video"
                 state={{ title: 'Bon! Chain of French Coffee Houses', videoUrl: 'https://youtube.com/embed/Pb6hHX_0XKU' }}>
                <div className="card-video-inner">
                    <img src={bon} alt="Bon!" />
                </div>
                <span className="card-link">Bon!</span>
                <p>Rebranding and e-commerce solution used by 180,000 entrepreneurs</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 22'}}>
            <div className="content">
                <Link to="/project" className="card-video"
                 state={{ title: 'Chekhov Sport Clubs', videoUrl: 'https://youtube.com/embed/Zyzz0qGrIJg' }}>
                <div className="card-video-inner">
                    <img src={chekhov} alt="Chekhov" />
                </div>
                <span className="card-link">Chekhov</span>
                <p>Redesign of the support portal and 30% improvement in users’ ability to get the answers they want</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 21'}}>
            <div className="content">
                <Link to="/project" className="card-video"
                 state={{ title: 'HP', videoUrl: 'https://youtube.com/embed/-z1fOkKGcjo' }}>
                <div className="card-video-inner">
                    <img src={hp} alt="HP" />
                </div>
                <span className="card-link">HP</span>
                <p>Iconography guidelines, assets and web design for the advanced and trusted digital forensics solution</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 20'}}>
            <div className="content">
                <Link to="/project" className="card-video"
                 state={{ title: 'Humo hockey club', videoUrl: 'https://youtube.com/embed/EEsVcFDerTM' }}>
                <div className="card-video-inner">
                    <img src={humoArena} alt="Humo Arena" />
                </div>
                <span className="card-link">Humo Arena</span>
                <p>Rebranding a solution that securely routes money abroad. Acquired by WorldFirst</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 20'}}>
            <div className="content">
                <Link to="/project" className="card-video"
                 state={{ title: 'Terra Pro', videoUrl: 'https://youtube.com/embed/rU3vyrjdxw0' }}>
                <div className="card-video-inner">
                    <img src={terraPro} alt="Terra Pro" />
                </div>
                <span className="card-link">Terra Pro</span>
                <p>Rebranding a solution that securely routes money abroad. Acquired by WorldFirst</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 19'}}>
            <div className="content">
                <Link to="/project" className="card-video"
                 state={{ title: 'Texnomart', videoUrl: 'https://www.youtube.com/embed/hyy-xw4sfzQ' }}>
                <div className="card-video-inner">
                    <img src={texnomart} alt="Texnomart" />
                </div>
                <span className="card-link">Texnomart</span>
                <p>Rebranding a solution that securely routes money abroad. Acquired by WorldFirst</p>
                </Link>
            </div>
        </div>
    </Fade>
    </div>
)

export default Listboard;